import React from 'react';
import styled from 'styled-components';
import Swiper from 'swiper';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { preventAllScroll } from 'tools/preventAllScroll';
import { catalogSlideListAction } from 'store/actions/catalogPage';
import { layoutContentHeight, layoutHeaderPaddingHorizontal } from 'style/const/layout@portal';
import 'swiper/swiper-bundle.css';

import {
    fullCardsInViewPort,
    catalogCardHeight,
    catalogCardWidth,
    catalogContentPaddingY,
    catalogCardGap,
} from '../Catalog.const/portal';
import { CatalogListItemPortal } from '../ListItem/CatalogListItem@portal';

const StyledRoot = styled.div`
    position: relative;

    height: ${layoutContentHeight}px;
    padding-top: ${catalogContentPaddingY}px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: start;

    overflow: hidden;
    --spatial-navigation-contain: contain;
`;

const StyledWrapper = styled.div`
    height: ${catalogCardHeight}px;
    // рястягиваем элемент по кол-ву айтемов для того, чтобы все айтемы попадали
    // на его слой и браузеру было проще это обсчитывать
    width: max-content;
`;

const StyledSwiperSlide = styled.div`
    width: ${catalogCardWidth}px;

    // swiper почему-то по умолчанию выносит все айтемы на отдельный слой
    // даже если они не двигаются независимо. Нужно побороть это, для лучшей производительности.
    transform: none !important;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export const CatalogListPortal: React.FC = () => {
    const dispatch = useDispatch();
    const swiperRef = React.useRef<Swiper | null>(null);
    const order = useSelector(({ catalogPagePortal }) => catalogPagePortal.order);
    const listIndexOffset = useSelector(({ catalogPagePortal }) => catalogPagePortal.listIndexOffset);
    const ref = React.useRef<HTMLDivElement>(null);

    /**
     * Важно использовать здесь хук useLayoutEffect из-за того, что
     * при первом рендере карусель может находить не на нулевой позиции.
     * Соответственно, нужно отрендерить ее за один раз, чтобы и браузер
     * не нагружать бесполезными пересчетами лаяута и карусель не дергалась.
     */
    React.useLayoutEffect(() => {
        if (ref.current instanceof HTMLDivElement) {
            swiperRef.current =
                swiperRef.current ||
                new Swiper('.swiper-container', {
                    slidesPerView: 'auto',
                    speed: 400,
                    spaceBetween: catalogCardGap,
                    initialSlide: listIndexOffset,
                    roundLengths: true,
                    /**
                     * Если не выключить этот параметр то не вызывается
                     * событие переключения на последний слайд.
                     */
                    normalizeSlideIndex: false,
                    slidesOffsetBefore: layoutHeaderPaddingHorizontal,
                    slidesOffsetAfter: layoutHeaderPaddingHorizontal,
                    on: {
                        slideChange: () => {
                            if (swiperRef.current) {
                                // синхронизируем стор со swiper'ом
                                dispatch(catalogSlideListAction(swiperRef.current.activeIndex));
                            }
                        },
                    },
                });
        }
    }, [dispatch, listIndexOffset]);

    /**
     * Синхронизируем swiper со стором. Важно это делать именно в хуке
     * useLayoutEffect. Это нужно для того чтобы синхронно внести
     * все изменения в DOM (помимо скролла карусели так же
     * пересчитываются бейджики индекса в карточках),
     * и браузер пересчитывал лаяут один раз.
     */
    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.slideTo(listIndexOffset, 400);
        }
    }, [listIndexOffset]);

    /**
     * Отслеживаем нажатия на клавиатуре и скроллим карусель.
     * Здесь нельзя вызывать карусель напрямую, так как вместе со скроллом
     * перерисовываются индексы на карточках. Поэтому все делаем через стор.
     */
    React.useEffect(() => {
        const onKeyDown = (event: KeyboardEvent) => {
            if (event.key === 'ArrowLeft') {
                if (swiperRef.current) {
                    const nextIndex = Math.max(0, swiperRef.current.activeIndex - fullCardsInViewPort);

                    dispatch(catalogSlideListAction(nextIndex));
                }
            }

            if (event.key === 'ArrowRight') {
                if (swiperRef.current) {
                    const nextIndex = Math.min(
                        order.length - fullCardsInViewPort,
                        swiperRef.current.activeIndex + fullCardsInViewPort,
                    );

                    dispatch(catalogSlideListAction(nextIndex));
                }
            }
        };

        window.addEventListener('keydown', onKeyDown);

        return () => {
            window.removeEventListener('keydown', onKeyDown);
        };
    }, [dispatch, order.length]);

    const items = React.useMemo(() => {
        return order.map((itemId) => (
            <StyledSwiperSlide key={itemId} className="swiper-slide">
                <CatalogListItemPortal id={itemId} />
            </StyledSwiperSlide>
        ));
    }, [order]);

    return (
        <StyledRoot onScroll={preventAllScroll} ref={ref} className="swiper-container">
            <StyledWrapper className="swiper-wrapper">{items}</StyledWrapper>
        </StyledRoot>
    );
};
